import React from 'react'
import { Container, Row, Col, Button } from 'react-bootstrap'

function AboutMeBroad() {
    return (
        <section id="about">
            <Container fluid>
                <Row>
                    <Col lg="7" className="d-flex align-items-center justify-content-center mt-3">
                        <div className="about-title">
                            <h2>Hi I'm Vachara Vongvittayamethkul</h2>
                            <h5>Web Developer</h5>
                            <Button onClick={() => { window.location.href = process.env.PUBLIC_URL + "#contact" }} className="about-button btn-dark">Contact Me</Button>
                            <a href={process.env.PUBLIC_URL + "/Vachara's Resume.pdf"} rel="noopener noreferrer" target="_blank"><Button className="about-button btn-dark">My Resume</Button></a>
                        </div>
                    </Col>
                    <Col lg="5" className="d-flex justify-content-end pr-lg-0">
                            <img className="d-flex justify-content-end" id="about-image" src={process.env.PUBLIC_URL + '/my_profile.png'} alt="profile" />
                    </Col>
                </Row>
            </Container>
        </section>
    )
}

export default AboutMeBroad