import React, { useState } from 'react';
import { Form, Button, Container } from 'react-bootstrap';

function ContactForm({ db }) {

    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [massage, setMassage] = useState('')
    const [sendEmail, setSendEmail] = useState(false)

    const sendMassage = () => {

        let dbCon = db.database().ref('/messages');
        dbCon.push({
            message: {
                'name': name,
                'email': email,
                'massage': massage
            }
        });
        setName('');
        setEmail('');
        setMassage('');
        setSendEmail(true)
    }


    return (
        <section id="contact">
            <Container>
                <h1 className="section-title">Contact Me</h1>
                <div style={{ width: "70%" }} className="m-auto">
                    <div class="alert alert-success" role="alert" style={{ display: (sendEmail)? "block	":"none" }}>
                        Your message has been successfully sent. We will contact you very soon!
                    </div>
                    <Form>
                        <Form.Group controlId="formBasicPassword">
                            <Form.Label>Name</Form.Label>
                            <Form.Control name="name" type="text" placeholder="Enter name" value={name} onChange={(event) => { setName(event.target.value) }} />
                        </Form.Group>

                        <Form.Group controlId="formBasicEmail">
                            <Form.Label>Email</Form.Label>
                            <Form.Control name="email" type="email" placeholder="Enter email" value={email} onChange={(event) => { setEmail(event.target.value) }} />
                        </Form.Group>

                        <Form.Group controlId="formBasicEmail">
                            <Form.Label>Message</Form.Label>
                            <Form.Control name="massage" as="textarea" row="4" placeholder="Enter Message" value={massage} onChange={(event) => { setMassage(event.target.value) }} />
                        </Form.Group>

                        <Button className="btn-dark" onClick={sendMassage}>
                            Send {'>>'}
                        </Button>
                    </Form>
                </div>
            </Container>
        </section>
    )
}

export default ContactForm;
