import React from 'react'
import { CardDeck, Container } from 'react-bootstrap'
import ProjectCard from './ProjectCard'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
    faWordpress, faPhp, faJs
} from '@fortawesome/free-brands-svg-icons';

function ProjectBroad() {
    return (
        <section id="project">        
            <Container>
            <h1 className="section-title">My Project</h1>
                <CardDeck className="d-flex justify-content-around">
                    <ProjectCard
                        link="https://www.cibgool.com"
                        img={process.env.PUBLIC_URL + '/cibgool.png'}
                        title="cibgool.com"
                        massage="This website has an idea for investors or those interested in keeping up to date with global news or news about investment in the world economy">
                        <FontAwesomeIcon icon={faWordpress} size="3x" className="program-icon" />
                        <FontAwesomeIcon icon={faPhp} size="3x" className="program-icon" />
                        <FontAwesomeIcon icon={faJs} size="3x" className="program-icon" />
                    </ProjectCard>
                </CardDeck>
            </Container>
        </section>
    )
}

export default ProjectBroad