import React from 'react'
import { Card, Container } from 'react-bootstrap'

function ProjectCard(props) {
    return (
        <div>
            <Card className="bg-secondary text-white" style={{ width: '20rem' }}><a href={props.link}>
                <Card.Img variant="top" src={props.img} /></a>
                <Card.Body>
                    <a href={props.link}><Card.Title className="">{props.title}</Card.Title></a>
                    <Card.Text>
                        {props.massage}
                    </Card.Text>
                    <Container className="d-flex justify-content-around">
                        {props.children}
                    </Container>
                </Card.Body>
            </Card>

        </div>
    )
}

export default ProjectCard