import React from "react"
import { Container } from "react-bootstrap"

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faDatabase } from '@fortawesome/free-solid-svg-icons';
import {
    faWordpress, faPhp, faJs, faGit, faJava
} from '@fortawesome/free-brands-svg-icons';


function SkillBroad() {
    return (
        <section id="skills">
            <Container>
                <h1 className="section-title">Programmer Skills</h1>
                <div className="d-flex justify-content-around mb-3">
                    <FontAwesomeIcon icon={faWordpress} size="4x" className="program-icon" />
                    <FontAwesomeIcon icon={faPhp} size="3x" className="program-icon" />
                    <FontAwesomeIcon icon={faJs} size="3x" className="program-icon" />
                    <FontAwesomeIcon icon={faJava} size="3x" className="program-icon" />

                </div>
                <div className="d-flex justify-content-around">
                    <FontAwesomeIcon icon={faGit} size="3x" className="program-icon" />
                    <FontAwesomeIcon icon={faDatabase} size="3x" className="program-icon" />
                </div>
            </Container>
        </section>
    )
}

export default SkillBroad